import Vue from 'vue';

/**
 * é€’å½’æå–ä¸€ä¸ªå¯¹è±¡ä¸­çš„æ‰€æœ‰å‡½æ•°
 * @param  {object} obj å¯¹è±¡
 * @return {object}     æ‰€æœ‰å‡½æ•°éƒ½å°†è¢«åŒ…è£…åˆ°è¿™ä¸ªå¯¹è±¡ä¸­
 */
function mergeManyObjToOneObj(obj) {
	var newObj = {};
	if (obj && typeof obj === 'object') {
		for (var f in obj) {
			if (typeof obj[f] === 'function') {
				newObj[f] = obj[f];
			}
			if (typeof obj[f] === 'object') {
				Object.assign(newObj, mergeManyObjToOneObj(obj[f]));
			}
		}
	}
	return newObj;
}

//å¯¼å…¥è‡ªå®šä¹‰çš„å…¨å±€æ··åˆ
var mixins = {
	methods: mergeManyObjToOneObj(require('./Methods/'))
};

//æ³¨å†Œå…¨å±€æ··åˆ
Vue.mixin(mixins);


