module.exports = {
    name: 'list-data',
    data() {
        return {
            batch_flag: true, //ç¬¦åˆæ‰¹é‡åˆ é™¤ä¸ºtrue,å¦åˆ™ä¸ºfalse
            batch_datas: [],
            batch_ids: [],

            image_host: this.ImageHost, //å¦‚æžœåˆ—è¡¨ä¸­æœ‰å›¾ç‰‡ï¼Œå¹¶ä¸”ä¸æ˜¯ç»å¯¹è·¯å¾„çš„è¯ï¼Œå¯ä»¥ä¼ å…¥è¿™ä¸ªå‚æ•°

            list: this.List, //åˆ—è¡¨æ•°ç»„
            fields: this.FieldList, //å­—æ®µæ•°ç»„
            selection: this.Selection, //æ˜¯å¦éœ€è¦æ‰¹é‡é€‰æ‹©
            expand:this.Expand,
            btn_info: this.BtnInfo,

            pagination: this.Pagination,
        }
    },
    methods: {
        /**
         * è¡¨æ ¼åˆ—è¡¨è§¦å‘CheckBoxçš„äº‹ä»¶
         * @param  {array} val å½“å‰é€‰ä¸­çš„ç”¨æˆ·ä¿¡æ¯æ•°ç»„ï¼Œæ¯ä¸ªå…ƒç´ æ˜¯ç”¨æˆ·ä¿¡æ¯å¯¹è±¡
         */
        onSelectionChange(val) {
            this.batch_datas = val;

            this.batch_ids = [];
            if (val.length) {
                this.batch_flag = false;
                for (var i = 0; i < val.length; i++) {
                    this.batch_ids.push(val[i].id);
                }
            } else {
                this.batch_flag = true;
            }

            /**
             * æ”¹å˜CheckBoxäº‹ä»¶ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯IDæ•°ç»„ï¼Œç¬¬äºŒä¸ªå‚æ•°äºŒç»´æ•°ç»„ï¼Œæ¯ä¸ªæ•°ç»„æ˜¯é€‰ä¸­çš„å¯¹è±¡
             */
            this.$emit('onSelectionChange', this.batch_ids, this.batch_datas);
            this.$emit('onSelectionChangeObj', {
                ids: this.batch_ids,
                datas: this.batch_datas
            });
        },



        /**
         * åˆ é™¤äº‹ä»¶
         * @param  {object || boolean} user  å½“å‰ä¿¡æ¯å¯¹è±¡æˆ–è€…ä¸ºå¸ƒå°”å€¼,ä¸ºå¸ƒå°”å€¼æ—¶ï¼Œä»£è¡¨æ˜¯æ‰¹é‡åˆ é™¤
         * @param  {number} index å½“å‰åˆ—è¡¨ç´¢å¼•
         */
        onDelete(data, index) {
            var opts = {};
            if (data === true) {
                opts.batch_ids = this.batch_ids;
                opts.batch_datas = this.batch_datas;
            } else {
                opts.data = data;
                opts.index = index;
            }

            /**
             * åˆ é™¤äº‹ä»¶ï¼Œå‚æ•°ä¸ºå¯¹è±¡
             * åˆ†ä¸¤ç§æƒ…å†µï¼Œä¸€ç§æ˜¯å•ä¸ªåˆ é™¤ï¼Œä¸€ç§æ˜¯æ‰¹é‡åˆ é™¤ï¼Œå±žæ€§åˆ†åˆ«å¦‚ä¸‹
             * 1ï¼šå•ä¸ªåˆ é™¤
             *     opts.data å½“å‰è¦åˆ é™¤çš„æ•°æ®å¯¹è±¡
             *     opts.index å½“å‰è¦åˆ é™¤çš„ç´¢å¼•
             *     opts.list å½“å‰åˆ—è¡¨æ•°ç»„
             * 2ï¼šæ‰¹é‡åˆ é™¤
             *     opts.batch_ids ä¸€ç»´æ•°ç»„ï¼Œéœ€è¦åˆ é™¤çš„IDæ•°ç»„
             *     opts.batch_datas äºŒç»´æ•°ç»„ï¼Œæ¯ä¸ªå…ƒç´ ä¸ºå¯¹è±¡(éœ€è¦åˆ é™¤çš„æ•°æ®å¯¹è±¡)
             */
            this.$emit('onDelete', opts);
        },

        onAdd(){
            this.$emit('onAdd', {});
        },

        /**
         * èŽ·å–è¡Œä¿¡æ¯äº‹ä»¶
         * @param  {object} row   å½“å‰è¡Œå¯¹è±¡
         * @param  {number} index å½“å‰è¡Œç´¢å¼•
         * @param  {array} list  å½“å‰åˆ—è¡¨æ•°ç»„
         */
        onGetInfo(row, index, list, type) {
            this.$emit('onGetInfo', {
                row,
                index,
                list,
                type
            });
        },


        onBtnEvent(type,row={},index=-1,list=[]){
            this.$emit('onBtn'+type, {
                row,
                index,
                list
            });
        },


        onUpdateBtn(data, index, list) {
            if (this.btn_info.update && this.btn_info.update.path) {
                var path = this.btn_info.update.path,
                    param_keys = this.btn_info.update.param_keys || [],
                    query_keys = this.btn_info.update.query_keys || [],
                    query = {};

                for (var i = 0; i < param_keys.length; i++) {
                    path += '/' + data[param_keys[i]];
                }
                for (var i = 0; i < query_keys.length; i++) {
                    query[query_keys[i]] = data[query_keys[i]];
                }

                // console.log(path);
                // console.log(query);

                this.$router.push({
                    path: path,
                    query: query
                });
            } else {
                this.onGetInfo(data, index, list, 'update');
            }

        },


        /**
         * å†…ç½®åˆ é™¤äº‹ä»¶æ‰§è¡ŒæˆåŠŸåŽï¼Œæ›´æ–°åˆ—è¡¨æ–¹æ³•
         * åˆ†ä¸¤ç§æƒ…å†µï¼Œä¸€ç§æ˜¯æ‰¹é‡åˆ é™¤ï¼Œä¸€ç§æ˜¯å•ä¸ªåˆ é™¤
         * 1ï¼šå•ä¸ªåˆ é™¤
         *     row å½“å‰éœ€è¦åˆ é™¤è¡Œçš„ç´¢å¼•
         * 2ï¼šæ‰¹é‡åˆ é™¤
         *     row ä¸€ç»´æ•°ç»„ï¼Œéœ€è¦åˆ é™¤çš„IDæ•°ç»„
         */
        onUpdateList(row) {
            if (!Array.isArray(row)) {
                this.list.splice(row, 1);
            } else {
                this.list = this.list.filter(function(item, idx) {
                    return row.indexOf(item.id) === -1;
                });
            }
        },


        /**
         * æ”¹å˜å½“å‰é¡µç äº‹ä»¶
         * @param  {number} page å½“å‰é¡µé¢
         */
        onChangeCurrentPage(page) {
            this.$emit('onChangeCurrentPage', page);
        },


        /**
         * æ”¹å˜æ¯é¡µæ˜¾ç¤ºçš„æ•°é‡äº‹ä»¶
         * @param  {number} page_size æ¯é¡µæ˜¾ç¤ºçš„æ•°é‡
         */
        onChangePageSize(page_size) {
            this.$emit('onChangePageSize', page_size);
        }
    },

    mounted() {
        // console.log(this.list);
    },

    /**
     * æŽ¥æ”¶å‚æ•°
     * @type {Object}
     */
    props: {
        ImageHost: {
            type: String,
            default: ''
        },
        List: {
            type: Array,
            required: true
        },
        FieldList: {
            type: Array,
            required: true
        },
        BtnInfo: {
            type: Object,
            default () {
                return {};
            }
        },
        Selection: {
            type: Boolean,
            default: false
        },
        Expand: {
            type: Object,
            default(){
                return {
                    show:false,
                    position:"left"
                };
            }
        },
        Pagination: {
            type: Object,
            default () {
                return {};
            }
        }
    },


    /**
     * ç›‘æŽ§å‚æ•°
     * @type {Object}
     */
    watch: {
        ImageHost(v) {
            if (v) {
                this.image_host = v;
            }
        },
        List(v) {
            if (v) {
                this.list = v;
            }
        },
        FieldList(v) {
            if (v) {
                this.fields = v;
            }
        },
        Selection(v) {
            this.selection = v;
        },
        Expand(v){
            this.expand=v;
        },
        BtnInfo(v) {
            this.btn_info = v;
        },
        Pagination(v) {
            this.pagination = v;
        }
    }
}


