import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);

// å¯¼å…¥å°è£…çš„å›žè°ƒå‡½æ•°
import {
	cbs,
	gbs
} from 'config/settings.js';
// åŠ¨æ€è®¾ç½®æœ¬åœ°å’Œçº¿ä¸ŠæŽ¥å£åŸŸå
Vue.axios.defaults.baseURL = gbs.host;

/**
 * å°è£…axiosçš„é€šç”¨è¯·æ±‚
 * @param  {string}   type      getæˆ–post
 * @param  {string}   url       è¯·æ±‚çš„æŽ¥å£URL
 * @param  {object}   data      ä¼ çš„å‚æ•°ï¼Œæ²¡æœ‰åˆ™ä¼ ç©ºå¯¹è±¡
 * @param  {Function} fn        å›žè°ƒå‡½æ•°
 * @param  {boolean}   tokenFlag æ˜¯å¦éœ€è¦æºå¸¦tokenå‚æ•°ï¼Œä¸ºtrueï¼Œä¸éœ€è¦ï¼›falseï¼Œéœ€è¦ã€‚ä¸€èˆ¬é™¤äº†ç™»å½•ï¼Œéƒ½éœ€è¦
 */
module.exports = function (type, url, data, fn, {
	cbFn,
	tokenFlag,
	errFn,
	host,
	headers,
	axios_opts
} = {}) {

	var options = {
		method : type,
		url    : host || url,
		headers: headers && typeof headers === 'object' ? headers : {}
	};

	//æ£€æµ‹æŽ¥å£æƒé™
	var api_flag = true;
	if (options.url && options.url.indexOf(gbs.host) && this.$store.state.user.userinfo.access_status === 1) {
		var url         = options.url.replace(gbs.host, '');
		var api_routers = this.$store.state.user.userinfo.api_routers;
		if (!api_routers || !api_routers.constructor === Object || !api_routers[url]) {
			api_flag = false;
		}
	}

	if (api_flag === true) {
		options[type === 'get' ? 'params' : 'data'] = data;

		// åˆ†å‘æ˜¾ç¤ºåŠ è½½æ ·å¼ä»»åŠ¡
		this.$store.dispatch('show_loading');

		if (tokenFlag !== true) {
			//å¦‚æžœä½ ä»¬çš„åŽå°ä¸ä¼šæŽ¥å—headersé‡Œé¢çš„å‚æ•°ï¼Œæ‰“å¼€è¿™ä¸ªæ³¨é‡Šï¼Œå³å®žçŽ°tokené€šè¿‡æ™®é€šå‚æ•°æ–¹å¼ä¼ 
			data.token = this.$store.state.user.userinfo.token;
			options.headers.token = this.$store.state.user.userinfo.token;
		}

		//axioså†…ç½®å±žæ€§å‡å¯å†™åœ¨è¿™é‡Œ
		if (axios_opts && typeof axios_opts === 'object') {
			for (var f in axios_opts) {
				options[f] = axios_opts[f];
			}
		}

		//å‘é€è¯·æ±‚
		Vue.axios(options).then((res) => {
				//console.log(res);
			if (res.status === 200) {
				//console.dir(res.data);	
				fn(res.data);
			} else {

				if (cbFn) {
					cbFn(res.data);
				}else{
					// è°ƒç”¨å…¨å±€é…ç½®é”™è¯¯å›žè°ƒ
					cbs.statusError.call(this, res.data);

					if (tokenFlag === true) {
						errFn && errFn.call(this);
					}
				}
			}
			this.$store.dispatch('hide_loading');
		}).catch((err) => {
			this.$store.dispatch('hide_loading');
			cbs.requestError.call(this, err);

			errFn && errFn.call(this);
		});
	} else {
		this.$alert('æ‚¨æ²¡ç”¨æƒé™è¯·æ±‚è¯¥æŽ¥å£ï¼', 'è¯·æ±‚é”™è¯¯', {
			confirmButtonText: 'ç¡®å®š',
			type             : 'warning'
		});
	}
};


