import {
	gbs
} from 'config/settings.js';

module.exports = {
	name   : 'addNews',
	data() {
		return {
			newsType:[],

			article_data: {
				NewsTitle  : '',
				NewsKey   : '',
				NewsCategory:'',
				status : true,
				Text: '',
			},
			temp        : {
				Text: ''
			},
			rules       : {
				 NewsTitle: [{
                    required: true,
                    message : 'æ–°é—»æ ‡é¢˜ä¸èƒ½ä¸ºç©ºï¼',
                    trigger : 'blur'
                }],
                NewsCategory: [{
                    required: true,
                    message : 'æ–°é—»ç±»åˆ«å¿…é€‰ï¼',
                    trigger : 'blur'
                }],
                NewsKey: [{
                    required: true,
                    message : 'å…³é”®å­—ä¸èƒ½ä¸ºç©ºï¼',
                    trigger : 'blur'
                }],
                Text:[{
                    required: true,
                    message : 'æ–°é—»å†…å®¹ä¸èƒ½ä¸ºç©ºï¼',
                    trigger : 'blur'
                }]
			},
			wangEditor  : {
				bar: [
					'source', '|',
					'bold', 'underline', 'italic', 'strikethrough', 'eraser', 'forecolor', 'bgcolor', '|',
					'quote', 'fontfamily', 'fontsize', 'head', 'unorderlist', 'orderlist', 'alignleft', 'aligncenter', 'alignright', '|',
					'link', 'unlink', 'table', 'emotion', '|',
					'img',
					'video',
					// 'location',
					'insertcode', '|',
					'undo', 'redo'
				]
			}
		}
	},
	methods: {
		getNewsCategory(){
               let _this = this;
               this.axios.post('/index.php?r=AuthCenter/news/get-all-news-group')
                .then((res)=>{
                var hh = JSON.parse(res.request.response); 
                    _this.newsType = hh.data;
               });
		},
		reBack(){
			this.$router.push('/module/acManage/news');
		},
		/**
		 * æäº¤è¡¨å•
		 * @param  {string} formName è¡¨å•åç§°
		 */
		onSubmit(formName){
			var ref = this.$refs[formName];
			var _this = this;
			ref.validate((valid) => {
				if (valid) {
					if (!this.temp.Text) {
						if ((this.article_data.Text.indexOf('<iframe') == -1 || this.article_data.Text.indexOf('</iframe>') == -1) && (this.article_data.Text.indexOf('<img') == -1)) {
							this.$message.error('æ–‡ç« å†…å®¹ä¸èƒ½ä¸ºç©ºï¼');
							return;
						}
						return;
					}
					this.axios.post('/index.php?r=AuthCenter/news/add-news',_this.article_data)
					.then((res)=>{
						var hh = JSON.parse(res.request.response);
						if(hh.status===200){
							//this.$message.success("æ·»åŠ æˆåŠŸï¼");
							this.$message({
								message:'æ·»åŠ æˆåŠŸï¼',
								type:'success'
							});
							this.$router.push('/module/acManage/news');
						}else{
							this.$message({
								message:hh.msg,
								type:'error'
							})
						}
					});
				}
			});
		}
		,
		setContent(html, text){
			this.article_data.Text = html;
			this.temp.Text         = text;
		}
		,
		reset_article(article){
			this.$refs[article].resetFields();
		}
	},

	mounted(){
		this.getNewsCategory();
		var self   = this;
		var editor = new wangEditor('article');

		editor.config.uploadImgFileName = 'article';
		editor.config.uploadImgUrl = gbs.host + '/index.php?r=common/upload';
		// é…ç½®è‡ªå®šä¹‰å‚æ•°ï¼ˆä¸¾ä¾‹ï¼‰
		/*editor.config.uploadParams = {
			token   : this.$store.state.user.userinfo.token,
			username: this.$store.state.user.userinfo.username
		};*/

		// è‡ªå®šä¹‰loadäº‹ä»¶
		editor.config.uploadImgFns.onload = (data) => {
			if (data.status === 200) {
				//alert(data.data.url);
				// ä¸Šä¼ å›¾ç‰‡æ—¶ï¼Œå·²ç»å°†å›¾ç‰‡çš„åå­—å­˜åœ¨ editor.uploadImgOriginalName
			//	var originalName = editor.uploadImgOriginalName || '';
				var originalName = editor.uploadImgOriginalName || '';
				// å¦‚æžœ resultText æ˜¯å›¾ç‰‡çš„urlåœ°å€ï¼Œå¯ä»¥è¿™æ ·æ’å…¥å›¾ç‰‡ï¼š
				editor.command(null, 'insertHtml', '<img src="' + gbs.host + data.data.url + '" alt="' + originalName + '" style="max-width:80%;"/>');
			} else {
				if (data.status === 404) {
					this.$message.error('ä¸Šä¼ é”™è¯¯ä¿¡æ¯ï¼štokenæ— æ•ˆï¼');
				} else {
					this.$message.error('ä¸Šä¼ é”™è¯¯ä¿¡æ¯ï¼š' + data.msg);
				}
			}

		};

		editor.config.uploadImgFns.onerror = (xhr) => {
			this.$message.error('ä¸Šä¼ é”™è¯¯ä¿¡æ¯ï¼šç½‘ç»œé”™è¯¯ï¼');
		};

		editor.config.menus = this.wangEditor.bar;

		//ç¼–è¾‘å™¨æ”¹å˜äº‹ä»¶æ—¶ï¼ŒåŒæ­¥æ›´æ–°æ–‡ç« å†…å®¹
		editor.onchange = function () {

			var text = this.$txt.text().replace(/(^\s*)|(\s*$)/g, ""),
				html = this.$txt.html();

			self.setContent(html, text);
		};

		editor.config.hideLinkImg = true;

		//è‡ªå®šä¹‰ä¸Šä¼ å›¾ç‰‡é”™è¯¯äº‹ä»¶
		editor.create();

		/*if (this.$route.query.id) {
			var data = {
				id: this.$route.query.id
			};
			this.$$api_article_findArticle(data, (data) => {
				// console.log(data);

				this.article_data        = data.article_info;
				this.article_data.status = data.article_info.status == 1 ? true : false;
				this.article_data.tabs   = data.article_info.tabs.split(',');

				$("#article").html(this.article_data.Text);
			});
		}*/
	}
}


