import Vue from 'vue';

// element-ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
Vue.use(ElementUI);

// router
import VueRouter from 'vue-router';
Vue.use(VueRouter);

// vuex
import Vuex from 'vuex';
Vue.use(Vuex);

//é¡µé¢é¡¶éƒ¨è¿›åº¦æ¡
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';


// root component
import App from './App';

//vuex store
import store from './store/';


import routes from './config/router.js';

//è‡ªåŠ¨æ³¨å†Œæœºåˆ¶ï¼Œæ ¸å¿ƒä»£ç 
import 'register/';
import 'mixin/';

const router = new VueRouter({
	routes
});

router.beforeEach((to, from, next) => {

	window.scroll(0, 0);

	var userinfo = store.state.user.userinfo;
	console.log(userinfo);
	//åŽŸæœ¬æ˜¯ï¼userinfo.tokenï¼Œå¦åˆ™ä¸èƒ½è¿›å…¥é¡µé¢ 
	if (userinfo.token && to.path !== '/login') {
		store.dispatch('remove_userinfo');
		next('/login');
	} else {

		//åˆ¤æ–­æ˜¯å¦å¼€å¯æƒé™
		if (userinfo.access_status === 1) {
			var web_routers = typeof userinfo.web_routers === 'object' ? userinfo.web_routers : {};

			//åˆ¤æ–­å½“å‰ç”¨æˆ·æ˜¯å¦æ‹¥æœ‰ç›®æ ‡é¡µé¢è®¿é—®æƒé™ï¼Œé˜²æ­¢ç”¨æˆ·å¼ºåˆ¶åœ¨åœ°å€æ è¾“å…¥è®¿é—®æ²¡æœ‰æƒé™çš„é¡µé¢
			if (!web_routers[to.path]) {
				ElementUI.MessageBox.alert('æ‚¨æ²¡ç”¨æƒé™è®¿é—®æ­¤é¡µé¢ï¼', 'è®¿é—®é”™è¯¯', {
					confirmButtonText: 'ç¡®å®š',
					cancelButtonText : 'å–æ¶ˆ',
					type             : 'warning'
				}).then(() => {
					if (userinfo.default_web_routers) {
						next(userinfo.default_web_routers);
					} else {
						store.dispatch('remove_userinfo');
						next('/login');
					}
				});
			} else {
				NProgress.start();
				next();
			}
		} else {

			if (userinfo.token && to.path === '/login') {
				next({
					path: '/demo/user/list'
				});
			} else {
				NProgress.start();
				next();
			}
		}

	}
})

router.afterEach(transition => {
	NProgress.done();
});

const appAdmin = new Vue({
	el    : '#app',
	data() {
		return {};
	},
	// template: '<App/>',
	router,
	store,
	render: h => h(App)
})


